.o-btn {
  display: inline-block;
  line-height: inherit;
  font-size: inherit;
  font-family: inherit;
  color: inherit;
  width: 6.854rem;
  height: 6.854rem;
  border: 4px solid $color-redux;
  background: $white-01;
  transition: transform 0.1s ease-out;

  &__kbd {
    display: block;
    font-family: $font-stack2;
    font-weight: bold;
  }

  &:hover {
    border-color: $color-react;
  }

  &:focus {
    border-color: $color-react;
    outline: 3px solid $color-react;
  }

  &:active,
  &.is-active {
    background: $color-react;
    color: $grey;
    border-color: $color-react;
    outline: none;
  }
}
